
.Items{
    display:flex;
    justify-content: space-between;
    margin:10px;
    width:180px;
    height:20px;
    border: 1px solid white;
    background-color: rgb(255, 152, 152);
    align-items: center;
    padding: 10px;;
}

.Items p {
    margin: 0px;
    font-family: fantasy;
    font-size: 18px;
}

.Items button{
    width:20px;
    height:20px;
    border-radius: 50%;
}

