.App {
  text-align: center;
}

.App-header {
  
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  /* for gradient background */
  background-color: #E4E9FD;
  background-image: -webkit-linear-gradient(65deg, #a6e3e9 50%, #e3fdfd 50%);
}
.wrapper{
  background-color: black;
  padding:10px;
  border-radius: 10%;
  box-shadow:0 1px 14px 0 green;

}
.Input-wrapper{
  display: flex;
  justify-content: space-between;
  width:250px;
  align-items: center;

}

.Input-wrapper input{
  border-radius: 4px;
  border:0;
  text-align: center;
  width: 200px;
  padding : 6px;
  height:20px;
  margin: 10px;
}

.Input-wrapper button{
  width: 25px;
  height:25px;
  border-radius: 50%;
  align-items: center;
}